/* global console, document, Excel, Office */
Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    document.getElementById("btnSyncData").onclick = mappingTask;
  }
});

function mappingTask() {
  return Excel.run(function (context) {
    const externalTable = context.workbook.tables.getItem("TblExternalData");
    const wbsTable = context.workbook.tables.getItem("TblWbs");

    // Lấy dữ liệu từ TblExternalData
    const externalRange = externalTable.getDataBodyRange();
    const wbsRange = wbsTable.getDataBodyRange();

    externalRange.load("values");
    wbsRange.load("values");

    // Tải hàng tiêu đề
    const headerRowRange = externalTable.getHeaderRowRange();
    headerRowRange.load("values");

    return context.sync().then(function () {
      const externalData = externalRange.values;
      const wbsData = wbsRange.values;
      const wbsTaskIDs = new Set(wbsData.map((row) => row[0])); // Giả sử TaskID ở cột đầu tiên

      // Lấy tiêu đề cột từ TblExternalData
      const headersExternal = headerRowRange.values[0];
      const keyIndex = headersExternal.indexOf("キー");
      const parentIdIndex = headersExternal.indexOf("親課題キー");
      const startDateIndex = headersExternal.indexOf("開始日");
      const endDateIndex = headersExternal.indexOf("期限日");

      // Duyệt qua dữ liệu từ TblExternalData
      for (const row of externalData) {
        const key = row[keyIndex]; // Lấy giá trị từ cột キー
        if (!wbsTaskIDs.has(key)) {
          const taskID = key;
          const taskName = "";
          const type = "";
          const pic = "";
          const reviewer = "";
          const pStartDate = row[startDateIndex];
          const pEndDate = row[endDateIndex];
          const pReleaseDate = "";
          const pEffort = "";
          const aStartDate = "";
          const aEndDate = "";
          const aEffort = "";
          const percentACompleted = "";
          const aReleaseDate = "";
          const parentID = row[parentIdIndex];
          const linkBackLog = "";
          const linkJira = "";
          const linkEvidence = "";

          // Thêm hàng mới vào bảng
          wbsTable.rows.add(null, [
            [
              taskID,
              taskName,
              type,
              pic,
              reviewer,
              pStartDate,
              pEndDate,
              pReleaseDate,
              pEffort,
              aStartDate,
              aEndDate,
              aEffort,
              percentACompleted,
              aReleaseDate,
              parentID,
              linkBackLog,
              linkJira,
              linkEvidence,
            ],
          ]);
        }
      }

      return context.sync().then(() => {
        showNotification("Task synced successfully!", "success");
      });
    });
  }).catch(function (error) {
    console.log(error);
    showNotification(`Error: ${error.message}`, "danger");
  });
}

function showNotification(message, type) {
  const notificationEl = document.getElementById("notification");
  notificationEl.textContent = message;
  notificationEl.className = `alert alert-${type} position-fixed bottom-0 start-50 translate-middle-x mb-3`;
  notificationEl.classList.remove("d-none");

  // Ẩn thông báo sau 3 giây
  // eslint-disable-next-line no-undef
  setTimeout(() => {
    notificationEl.classList.add("d-none");
  }, 3000);
}
